import { _decorator, Button, Component, instantiate, Node, Prefab, Animation, Label } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('shoot')
export class shoot extends Component {
    
    @property(Prefab)
    bulletPrefab: Prefab = null;

    @property(Node)
    enemy: Node = null;

    @property(Label)
    hit: Label = null;
    // 定義bulletAry為陣列，用於存放擊中事件
    bulletAry:Node[] = [];

    onLoad() {
        // 將HIT字串設定為空值
        this.hit.string = "";
        // 將bulletAry陣列設定為空值
        this.bulletAry =[];
    }

    // @property(Button)
    // shoot_btn: Button=null;

    start() {
        // this.shoot_btn.node.on('click', this.onclickshoot,this);
    }

    update(deltaTime: number) {
        for(let i=0; i < this.bulletAry.length; i++ ){
            let addbullet = this.bulletAry[i-1];
            if(this.hittime(addbullet, this.enemy)){
                // 清除陣列中檢查完的擊中事件
                this.bulletAry.splice(i,1);
            };
        }
    }

    onclickshoot(evt){
        // 確認按鍵有吃到此函數
        console.log("按下射擊");
        // 複製bulletPrefab(子彈)
        let addbullet = instantiate(this.bulletPrefab);
        // 在場景中新增子彈節點
        this.node.addChild(addbullet);
        // 子彈消失
        let bulletAni = addbullet.getComponent(Animation);
        bulletAni.on(Animation.EventType.FINISHED, () => {
            addbullet.parent = null;
        }, this);
        // 將擊中事件放進陣列
        this.bulletAry.push(addbullet);
        
    }

    collision(addbullet, enemy){
        // 計算子彈與敵人間的距離
        let dist = addbullet.position.subtract(enemy.position).length();
        // 將計算結果傳入dist
        return dist;
    }

    hittime(addbullet, enemy){
        if(this.collision(addbullet, enemy) < 10){
            console.log("打中敵人");
            let bulletAni = addbullet.getComponent(Animation);
            // 停止監聽子彈移動事件
            bulletAni.off(Animation.EventType.FINISHED, () => {
                // 將子彈父節點清空
                addbullet = null;
            }, this); 
            // 顯示hit
            this.hit.string = "hit";
            // 1秒後hit消失
            this.scheduleOnce(function(){
                this.hit.string=""
            }.bind(this),1)

            return true;
        }
        else{
            return false;
        }

    }

//28.38


}


